import { AfterViewInit, OnInit, Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';

import itemList from '../../../assets/json/item.json';
import servDropList from '../../../assets/json/serv_drop.json';
import dropList from '../../../assets/json/drop.json';

export interface TableData {
  id: number;
  name: string;
  description: string;
  memo: string;
}

@Component({
  selector: 'mms-table-list',
  styleUrls: ['mms-table-list.component.css'],
  templateUrl: 'mms-table-list.component.html',
})
export class MmsTableListComponent implements OnInit {

  displayedColumns: string[] = ['name', 'description'];
  targetControl = new FormControl();
  dataSource: MatTableDataSource<TableData>;
  input: string = '';

  constructor () {
    // Assign the data to the data source for the table to render
    this.dataSource = new MatTableDataSource();
    document.getElementsByTagName('mat-sidenav-content')[0].scrollTo(0, 0);
  }

  ngOnInit () {
  }

  ngAfterViewInit() {
    const datas = dropList.root.drop.filter((drop: any) => {
      return typeof drop !== 'string' && drop[''] && drop[''].indexOf('') === 0 && +drop[''].slice(-2) < 27
    }).map((drop: any) => {
      return {
        id: drop[''],
        name: drop[''],
        description: this.getDropDescription(drop),
        memo: this.getDropMemo(drop)
      };
    });
    for (const data of datas) {
      this.dataSource.data.push(data);
    }
    this.dataSource.filter = '';
  }

  applyFilter() {
    this.dataSource.filter = this.input.trim().toLowerCase();
  }

  getDropDescription (drop: any) {
    const drops = [];
    for (let i = 1; i <= 40; i++) {
      if (drop['item'+i]) drops.push({ id: drop['item'+i], count: drop['count'+i], i});
    }
    return drops.map((d) => itemList.root[''].find((item: any) => item[''] === d.id)[''] + '(' + this.getProbPercent(drop, d) + '%)').join(', ');
  }

  getProbPercent (drop: any, d: any) {
    const serv_drop = servDropList.root.drop.find((sd) => sd[''] === drop['']);
    return Math.round(+serv_drop['prob'+d.i] / serv_drop['factor'] * 10000) / 100;
  }

  getDropMemo (drop: any) {
    const drops = [];
    for (let i = 1; i <= 40; i++) {
      if (drop['item'+i]) drops.push({ id: drop['item'+i], count: drop['count'+i]});
    }
    return drops.map((d) => {
      const item = itemList.root[''].find((item: any) => item[''] === d.id);
      switch(item['']) {
        case '':
          if (item['']) return 's' + (item[''] ? item[''] : 0) + '';
          if (item['']) return 's' + (item[''] ? item[''] : 0) + '';
          return '';
        case '':
          if (item['2']) return 's' + (item[''] ? item[''] : 0) + ', ' +  's' + (item[''] ? item[''] : 0) + '';
          if (item['4']) return 's' + (item[''] ? item[''] : 0) + ', ' +  's' + (item[''] ? item[''] : 0) + '';
          return '';
        case '':
          return 's' + (item[''] ? item[''] : 0) + ', ' +  's' + (item[''] ? item[''] : 0) + '';
        case '':
          if (item['']) return 's' + (item[''] ? item[''] : 0) + ', ' +  's' + (item[''] ? item[''] : 0) + '';
          if (item['']) return 's' + (item[''] ? item[''] : 0) + ', ' +  's' + (item[''] ? item[''] : 0) + '';
          return '';
        case '':
          return 's' + (item[''] ? item[''] : 0) + ', ' +  's' + (item[''] ? item[''] : 0) + '';
        case '':
          return 's' + (item[''] ? item[''] : 0) + '';
        case '':
          return 's' + (item[''] ? item[''] : 0) + ', ' +  's' + (item[''] ? item[''] : 0) + '';
        case '':
          return 's' + (item[''] ? item[''] : 0) + '';
        case '':
          return 's' + (item[''] ? item[''] : 0) + '';
        case '':
          return 's' + (item[''] ? item[''] : 0) + '';
        case '':
          return 's' + (item[''] ? item[''] : 0) + '';
        case '':
          return 's' + (item[''] ? item[''] : 0) + '';
        case '':
          return 's' + (item[''] ? item[''] : 0) + '';
        case '':
          return 's' + (item[''] ? item[''] : 0) + ', ' +  's' + (item[''] ? item[''] : 0) + '';
        case '':
          return 's' + (item[''] ? item[''] : 0) + ', ' +  's' + (item[''] ? item[''] : 0) + '';
        case '':
          return 's' + (item[''] ? item[''] : 0) + '';
        case '':
          return 's' + (item[''] ? item[''] : 0) + ', ' +  's' + (item[''] ? item[''] : 0) + '';
        default:
          return '';
      }
    }).join(', ');
  }

}
