import { OnInit, Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import itemList from '../../assets/json/item.json';
import dropList from '../../assets/json/drop.json';
import monsterList from '../../assets/json/monster.json';
import questList from '../../assets/json/quest.json';
import questSteplistList from '../../assets/json/quest_steplist.json';
import questStepinfoList from '../../assets/json/quest_stepinfo.json';


export interface ItemData {
  id: number;
  name: string;
  type: string
  description: string;
}

/**
 * @title Data table with sorting, pagination, and filtering.
 */
@Component({
  selector: 'item',
  styleUrls: ['item.component.css'],
  templateUrl: 'item.component.html',
})
export class ItemComponent implements OnInit {

  id: any;
  item: any;
  drops: any;
  infos: any;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
  ) {
    document.getElementsByTagName('mat-sidenav-content')[0].scrollTo(0, 0);
  }

  ngOnInit () {
    this.id = this.route.snapshot.paramMap.get('id');
    this.item = itemList.root[''].find((item: any) => { return item[''] === this.id; });
    this.drops = dropList.root.drop.filter((drop: any) => {
      for (let i = 1; i <= 40; i++) {
        const d = drop['item'+i];
        if (d && d === this.id) return true;
      }
      return false;
    }).map((drop: any) => {
      const monster = monsterList.root.npc.find((monster: any) => { return monster[''] === drop['']});
      if (!monster || +drop[''] > 4213 ) return { type: 'drop', data: drop }
      return { type: 'monster', data: monster };
    });

    console.log('itemname', this.item['']);

    this.infos = questStepinfoList.root.info.filter((info: any) => {
      return info[''] === '' && info[''] === this.item[''];
    }).map((info: any) => {
      return {
        steps: questSteplistList.root[''].filter((steplist: any) => {
          if (steplist['item1'] && steplist['item1'] === info['']) return true;
          if (steplist['item2'] && steplist['item2'] === info['']) return true;
          if (steplist['item3'] && steplist['item3'] === info['']) return true;
          if (steplist['item4'] && steplist['item4'] === info['']) return true;
          if (steplist['item5'] && steplist['item5'] === info['']) return true;
          return false;
        }).map((steplist: any) => {
          return questList.root[''].filter((quest) => {
            if (quest['00'] && quest['00'] === steplist['']) return true;
            if (quest['01'] && quest['01'] === steplist['']) return true;
            if (quest['02'] && quest['02'] === steplist['']) return true;
            if (quest['03'] && quest['03'] === steplist['']) return true;
            if (quest['04'] && quest['04'] === steplist['']) return true;
            if (quest['05'] && quest['05'] === steplist['']) return true;
            if (quest['06'] && quest['06'] === steplist['']) return true;
            if (quest['07'] && quest['07'] === steplist['']) return true;
            if (quest['08'] && quest['08'] === steplist['']) return true;
            return false;
          });
        }),
        count: info['']
      };
    });

    console.log('infos', this.infos);

    console.log('drops', this.drops);
  }

}
